import React, { Component } from "react";
import Nav from "./Nav";
import "./sidebar.css";

export default class Sidebar extends Component {
  render() {
    const { path } = this.props;
    return <div className="Sidebar">
        <Nav path={path} />
        <div className="first-col" />
        <div className="second-col">
          <p className="main-number">
            0{path === "/" ? <span className="animate">
                1
              </span> : null}
            {path === "/experience" ? <span className="animate">
                2
              </span> : null}
          {path === "/education" ? <span className='animate'>3</span> : null}
          {path === "/publications" ? <span className='animated bounce'>4</span> : null}
          </p>
        </div>
      </div>;
  }
}
